@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,600,800&display=swap);
body{
    font-family: 'Nunito Sans', sans-serif;
}
#navbar{
    width: 100%;
    height: 70px;
    box-shadow: 0px -2px 23px -13px #777
}

ul{
    list-style: none;
    width: 100%;
    margin: 0;
    padding: 0;
    height: 100%;
}
ul li{
    display: inline-block;
    padding: 22px 5%;
}
.title{
    float: left;
    font-weight: 800;
    font-size: 118%;
}
.light-toggle{
    float: right;
    font-weight: 600;
    font-size: 96%;
    cursor: pointer;
    position: relative;
}

.moon-icon{
    position: absolute;
    left: -6px;
    bottom: 50%;
    width: 16px;
    height: 16px;
    -webkit-transform: translateY(50%);
            transform: translateY(50%);
}

/* MOBILE VERSION */
@media only screen and (max-width: 375px) {
    ul li{
        padding: 22px 20px;
    }
    .title{
        font-size: 16px;
    }
}

.light-theme{
    background-color: #fff !important;
    color: hsl(200, 15%, 8%);
    box-shadow: 0 0 6px -2px #777;
}

.dark-theme{
    background-color: hsl(207, 26%, 17%);
    color: hsl(0, 0%, 100%);
    box-shadow: 0 0 6px -2px #000;
}

.outerInput{
    width: 100%;
    display: grid;
    position: relative;
    margin-top: 20px;
    grid-template-columns: 5% 1fr;
}
.searchInput{
    width: calc(95% - 55px);
    padding-left: 55px;
    height: 40px;
    font-size: 14px;
    grid-column-start: 2;
    border: none;
    border-radius: 5px;
}
.searchInput:focus{
    outline: none;
}
.searchIcon{
    width: 14px;
    margin-left: 100%;
    -webkit-transform: translateX(22px);
            transform: translateX(22px);
    padding-top: 15px;
    z-index: 1;
}
#wrapper{
    width: 100%;
    height: 100vh;
}

.light-theme{
    background-color: hsl(0, 0%, 98%);
    color: hsl(200, 15%, 8%);
}

.dark-theme{
    background-color: hsl(207, 26%, 17%);
    color: hsl(0, 0%, 100%);
}
body{
    margin: 0;
    padding: 0;
    border: 0;
}
